<template>
  <section class="s-services">
    <h1 class="h1-responsive font-weight-bold text-center mb-4 mt-0">Automatic annotations from our model</h1>

    <div class="ml-md-5 mr-md-5 text-container">      
      <div class="mr-md-3 text-col">
        <b-table :items="images" :fields="fields" hover @row-clicked="rowClicked" class="results">
          <template slot="filename" slot-scope="data">          
            <span :class="[(data.index === value) ? 'text-selected' : '']">{{ data.item.filename }}</span>
          </template>

          <template slot="score" slot-scope="data">
            <span :class="[(data.index === value) ? 'text-selected' : '']" style="text-align: center">
              {{ data.item.res[0].className }} 
            </span>
          </template>

          <template slot="confidence" slot-scope="data">
            <span :class="[(data.index === value) ? 'text-selected' : '']">
              {{ (data.item.res[0].probability*100) | round(2) }}%
            </span>
          </template>
        </b-table>
      </div>
      
      <div class="image-col">
        <img id="selectedImage" :src="images[value].img.src" :width="width" :height="height" alt="Knee cap">

        <div class="mt-2 text-center">
          <span v-if="tool == 'MRI'">femoral cartilage area: </span>
          <strong style="font-size: 2em">{{ images[value].res[0].className }}</strong><br/>
          <span v-if="tool == 'KL'">(confidence = {{ (images[value].res[0].probability*100) | round(2) }}%)</span>
        </div>
      </div>
    </div> 

    <SectionLine color="#39b54a" top/>    
  </section>  
</template>

<style>
div.text-container {
  display: flex; 
  justify-content: center;
  flex-flow: row wrap;
}
div.text-col {
  flex: 1 1; 
  max-width: 600px;
}
div.image-col {
  flex: 0 0;
  min-width: 256px;
}
.s-services {
  padding-top: 100px;
}
.s-services h1 {
  color: rgb(94, 94, 94);
}
/* compact table for mobiles */
@media only screen and (max-width: 400px) {
  table.results {
    display: block;
    overflow-x: scroll;
  }
}
table.results th,
table.results td {
  color:  rgb(94, 94, 94);
  border-bottom: 1px solid rgb(185, 185, 185);
}
table.results th {
  border-top: 1px solid rgb(185, 185, 185);
}

table.results tr:hover {
  cursor: pointer;
}
</style>

<script>
export default {
  name: 'Results',
  props: {
    value: Number, // selected index
    images: Array,
    width: Number,
    height: Number,
    tool: String
  },
  data () {
    var data = {
        sortBy: 'filename',
	fields: [
          { key: 'filename', label: 'Filename', sortable: true },
          { key: 'score', label: 'KL Score', sortable: false, thClass: 'text-center', tdClass: 'text-center' },
      ]
    }
    if (this.tool == "KL"){
      data["fields"].push({ key: 'confidence', label: 'Confidence', sortable: false, thClass: 'text-center', tdClass: 'text-center' })
    }
    else {
      data["fields"][1]["label"] = "Femoral cartilage area"
    }
    return data
  },
  methods: {
    rowClicked (item, index, event) {
      this.$emit('input', index)
    }
  }
}
</script>

<style>
.text-selected {
  font-weight: bold;
}
.modal {
    position: fixed;
    top: 40% !important;
}
</style>