*/
export default {
  name: 'KLTool',
  mixins: [
    DICOMConverter,    
    ToolMixin
  ],  
  components: {
    ToolSelectFile,
    Results,
    Creators,
    ContactForm,
    Status,
  },
  data () {
    return {
      imageSize: 299,
      splitImages: true
    }
  },
  created () {
    // Common code moved to ToolMixin.created()
    this.images.push({
      filename: "true_label_2.png",
      img: { src: require('@/assets/img/test/true_label_2.jpg') },
      res: [
        { className: "2",
          probability: 0.5556 }
      ],
      time: 0
    });
    this.images.push({
      filename: "true_label_4.png",
      img: { src: require('@/assets/img/test/true_label_4.jpg') },
      res: [
          { className: "4",
            probability: 0.7899}
        ],
      time: 0
    });
  },
  computed: {
    mobilenet () {
      return this.$store.state.klTool.mobilenet
    },
    networkStatus: {
      get: function () {
        return this.$store.state.klTool.networkStatus
      },
      set: function (value) {
        this.$store.commit('setKLToolStatus', value)
      }
    }
  }, 
  methods: {
    watchHandler (self, promises, value) {
      for (let i = 0; i < value.length; i++) {
        promises.push(new Promise(resolve => {
          self.processImage(value[i]).then((image) => {
	    var canvas = document.createElement('canvas');
	    var context = canvas.getContext('2d');
	    canvas.width = image.width;
	    canvas.height = image.height;
	    context.drawImage(image, 0, 0 );
	    self.splitImages = countKneesInCanvas(canvas) > 1;
            if (self.splitImages) {
              let canvasLeft = document.createElement('canvas')
              let canvasRight = document.createElement('canvas')
              let ctxLeft = canvasLeft.getContext('2d')
              let ctxRight = canvasRight.getContext('2d')
              let half = Math.round(image.naturalWidth / 2)
              canvasLeft.width = half
              canvasRight.width = image.naturalWidth - half            
              canvasLeft.height = canvasRight.height = image.naturalHeight
              ctxLeft.drawImage(image, 
                                0, 0, half, image.naturalHeight, // source
                                0, 0, half, image.naturalHeight) // dest
              ctxRight.drawImage(image, 
                                half + 1, 0, image.naturalWidth - half, image.naturalHeight, // source
                                0, 0, image.naturalWidth - half, image.naturalHeight) // dest
              let imgLeft = new Image
              imgLeft.name = image.name + '_left'
              let imgRight = new Image
              imgRight.name = image.name + '_right'
              let halfPromises = []
              halfPromises.push(new Promise(res => {
                imgLeft.onload = () => {
                  self.predict(imgLeft, imgLeft.name, res)
                }
              }))
              halfPromises.push(new Promise(res => {
                imgRight.onload = () => {
                  self.predict(imgRight, imgRight.name, res)
                }
              }))              
              imgLeft.src = canvasLeft.toDataURL('image/png')
              imgRight.src = canvasRight.toDataURL('image/png')
              Promise.all(halfPromises).then(values => {
                resolve(true)
              })
              // Splitting test
              //this.$refs.test1.src = imgLeft.src
              //this.$refs.test2.src = imgRight.src
            } else {
              self.predict(image, image.name, resolve)
            }          
          })          
        }))
      }
    },
    predict (image, name, resolve) {
      // prediction
      const logits = tf.tidy(() => {
        let img = this.preprocess(image);
        const batched = img.reshape([1, this.imageSize, this.imageSize, 3]);
        return this.mobilenet.predict(batched);
      })
      this.getTopKClasses(logits, 5).then(classes => {
        // remove excess items if needed
        if (this.images.length >= 5) {
          // this.images.length.shift()
        }
        this.images.push({
          filename: name,
          img: image,
          res: classes,
        })
        this.selected = this.images.length-1
        resolve(true)
      })
    },
    getTopKClasses (logits, topK) {
      //const values = await logits.data()
      return logits.data().then(values => {
        const valuesAndIndices = []
        for (let i = 0; i < values.length; i++) {
          valuesAndIndices.push({value: values[i], index: i})
        }
        valuesAndIndices.sort((a, b) => {
          return b.value - a.value
        })
        const topkValues = new Float32Array(topK)
        const topkIndices = new Int32Array(topK)
        for (let i = 0; i < topK; i++) {
          topkValues[i] = valuesAndIndices[i].value
          topkIndices[i] = valuesAndIndices[i].index
        }
        const topClassesAndProbs = [];
        for (let i = 0; i < topkIndices.length; i++) {
          topClassesAndProbs.push({
            className: topkIndices[i],
            probability: topkValues[i]
          })
        }
        return topClassesAndProbs
      })
    }
  }
}</script>
