import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
import toasts from '@/components/Toasts/ToastStoreModule.js'
import * as tf from '@tensorflow/tfjs'

const statusLoading = 'Loading... (wait up to 30 seconds)'
const statusReady = 'Ready! You can upload images now'

export default new Vuex.Store({
  modules: {
    toasts
  },
  state: {
    klTool: {
      mobilenet: false,
      networkStatus: statusLoading
    },
    mriTool: {
      mobilenet: false,
      networkStatus: statusLoading
    },
    links: {
      facebook: 'https://www.facebook.com/Saliencyai-308526986484182/',
      twitter: 'https://twitter.com/saliencyai',
      linkedin: 'https://www.linkedin.com/company/19134331/'
    }
  },
  mutations: {
    setKLToolNet (state, net) {
      state.klTool.mobilenet = net
    },
    setKLToolStatus (state, status) {
      state.klTool.networkStatus = status
    },
    setMRIToolNet (state, net) {
      state.mriTool.mobilenet = net
    },
    setMRIToolStatus (state, status) {
      state.mriTool.networkStatus = status
    }
  },
  actions: {
    warmUpKLNet (context, params) {
      tf.loadLayersModel(params.modelPath).then( (value) => {
        context.commit('setKLToolStatus', statusReady)
        context.commit('setKLToolNet', value)
      })  
    },
    warmUpMRINet (context, params) {
      tf.loadLayersModel(params.modelPath).then( (value) => {
        context.commit('setMRIToolStatus', statusReady)
        context.commit('setMRIToolNet', value)
      })  
    }
  }
})
