<template>
  <section id="demo" class="header-image py-4">
    <b-container class="text-light my-5">
      <b-row align-h="center">
        <b-col cols="12" md="8" class="mb-2 text-center">
          <div class="section-header has-bottom-sep">
            <h2>{{ header }}</h2>
          </div>
          <div style="margin: 0.5em;"><a style="text-decoration: underline" href="/license/">Before using our software read and accept our license agreement.</a></div>
          <h4>Try it out! Pick an image to analyze</h4>
        </b-col>
      </b-row>

      <b-row align-h="center" >
        <b-col class="mx-sm-3 mb-2" cols="12" sm="10" md="8" lg="6">
          <b-form-file v-model="files" 
                       :state="Boolean(files)" 
                       :disabled="busy" 
                       placeholder="Choose or drag-and-drop a file..." 
                       :multiple="multiple"
                       :accept="accept"/>                       
        </b-col>
      </b-row>


      <b-row align-h="center">            
        <b-col class="text-center" style="font-weight: bold;">
          The analysis is run on your computer - no images are sent out!
        </b-col>
      </b-row>  
      <b-row align-h="center">            
        <b-col class="text-center" style="">
	  It can take up to 30 seconds to process an image depending on your computer configuration.
        </b-col>
      </b-row>  
    </b-container>

    <!-- Other blocks -->
    <!--<img ref="test1"/>
    <img ref="test2"/>-->
    <SectionLine bottom/>
  </section>    
</template>

<script>
import Status from '@/components/Status'

export default {
  name: 'ToolSelectFile',
  components: {
    Status
  },
  props: {
    busy: Boolean,
    header: String,
    statusText: String,
    value: Array,
    multiple: {
      type: Boolean,
      default: true
    },
    accept: String
  },
  computed: {
    files: {
      get () {
        return this.value
      },
      set (value) {
        this.$emit('input', this.multiple ? value : [value])
      }
    }
  }
}
</script>

